
<template>
	<div id="app" class="map-container">
		<img id="myimage" src="../images/ArdosSectorMap.jpg" alt="Sector Map" usemap="#InfoLayer" class="map"/>
		<svg class="overlay" width="100%">
      <polygon shape="poly" class='overlay-zone' :class="zoneColour('orbitalStation')" @click="rotateControl('orbitalStation')"
						:points="coords.orbitalStation" href="#OrbitalStation" alt="Some Text" />
      <polygon shape="poly" class="overlay-zone" onClick="rotateControl()"
						:points="coords.commsRelay" href="#CommsRelay" alt="More Text" />
				<polygon shape="poly" class="overlay-zone" onClick="rotateControl()"
						:points="coords.ancientRuins" href="#AncientRuins" alt="Ancient Ruins" />
				<polygon shape="poly" class="overlay-zone" onClick="rotateControl()"
						:points="coords.hiveCity" href="#Pelius" alt="Hive City of Pelius" />
				<polygon shape="poly" class="overlay-zone" onClick="rotateControl()"
						:points="coords.commandComplex"  
						href="#CommandComplex" alt="Command Complex" />
				<polygon shape="poly" class="overlay-zone" onClick="rotateControl()"
						:points="coords.wasteland" href="#Wastelands" alt="Wastelands of Pelius" />
				<polygon shape="poly" class="overlay-zone" onClick="rotateControl()"
						:points="coords.shippingYards"
						href="#ShippingYards" alt="Siora Polat Shipping Yards" />
				<polygon shape="poly" class="overlay-zone" onClick="rotateControl()"
						:points="coords.solarPlant" 
						href="#SolarPlant" alt="Siora Power Plant" />
				<polygon shape="poly" class="overlay-zone" onClick="rotateControl()"
						:points="coords.aghoru" 
            href="#Aghoru" alt="Aghoru Archipelago" />
				<polygon shape="poly" class="overlay-zone" onClick="rotateControl()"
						:points="coords.peridos"
						href="#Peridos" alt="Peridos Gap" />
				<polygon shape="poly" class="overlay-zone" onClick="rotateControl()"
						:points="coords.sancour" 
            href="#Sancour" alt="Sancour Industrial Zone" />
				<polygon shape="poly" class="overlay-zone" onClick="rotateControl()"
						:points="coords.gasFields" 
						href="#GasFields" alt="Lavatina Gas Fields" />
		</svg>
	</div>
</template>

<script>
  import coords from './appCoords';
  import { LMap, LTileLayer, LMarker, LPolygon } from 'vue2-leaflet';
	/* eslint-disable vue/no-unused-components */
  export default {
    name: 'ArgosMap',
    components: { 
      LMap, LTileLayer, LMarker, LPolygon
    },
    data() {
      return {
        coords: {...coords},
        control: {
          "orbitalStation": 0,
          "commsRelay": 0,
          "ancientRuins": 0,
          "hiveCity": 0,
          "commandComplex": 0,
          "wasteland": 0,
          "shippingYards": 0,
          "solarPlant": 0,
          "aghoru": 0,
          "peridos": 0,
          "sancour": 0,
          "gasFields": 0
        },
        colours: {
          0: "red",
          1: "blue",
          2: "purple",
          3: "green",
        }
      };
    },
    methods: {
      rotateControl(zone) {
        this.control[zone] <= 2 ? this.control[zone]++ : this.control[zone] = 0; 
      },
      zoneColour(zone) {
        console.log('!!! zone colour: zone, result:', zone, this.colours[this.control[zone]]);
        return this.colours[this.control[zone]]
      }
    }
  }
</script>

<style lang="scss" scoped>
  .map-container {
    height: 2273px;
  }
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    height: 2273px;
  }
  .overlay-zone {
    opacity: 0.3;
    fill:lime;
    stroke:purple;
    stroke-width:4;

    &:hover {
      opacity: 0.5;
    }

    &.red {
      fill: #ff0000;
    }

    &.blue {
      fill: #0000ff;
    }

    &.purple {
      fill: #800080;
    }

    &.green {
      fill: #008000;
    }
  }

</style>